@import url(https://cdn.rawgit.com/konpa/devicon/df6431e323547add1b4cf45992913f15286456d3/devicon.min.css);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html{scroll-behavior:smooth}body{font-family:'Montserrat', sans-serif}.hero-container{display:flex;align-items:center;justify-content:center;height:100vh;font-size:50px;text-align:center}#my-photo{background-size:250px;background-repeat:no-repeat;width:250px;height:250px;text-align:center;box-shadow:0 4px 8px 0 rgba(0,0,0,0.7),0 6px 20px 0 rgba(0,0,0,0.3);background-image:url("https://i.imgur.com/1Tmyccz.jpg");border-radius:50%}#my-hobbies{font-size:15px}.left-column{width:70%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:space-around;text-align:center;background:radial-gradient(#172b3f, #16273b);color:white}.right-column{width:30%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;color:#172b3f;background-size:100%;background-image:url("https://image.freepik.com/free-vector/bright-background-with-dots_1055-3132.jpg")}.tech-skills-container{flex-direction:column;display:flex;align-items:center;justify-content:center;text-align:center;height:100vh}#page-links{display:flex;align-items:center;width:100%;justify-content:space-around;text-align:center;font-size:20px}#about-me{border:#00203fff 2px solid;font-size:16px;padding:50px;margin:50px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.7),0 6px 20px 0 rgba(0,0,0,0.3);background:#ececec;color:#00203fff;opacity:0.75}#icon{font-size:50px;height:50px;width:50px;text-align:center;margin:30px}.icon-container{flex-wrap:wrap;display:flex;align-items:center;justify-content:center;text-align:center;border:#00203fff 3px solid;box-shadow:0 4px 8px 0 rgba(0,0,0,0.7),0 6px 20px 0 rgba(0,0,0,0.3)}#my-skills{margin-bottom:100px;font-size:50px;font-weight:bold}a{color:inherit;text-decoration:none;display:inline-block;position:relative;color:white}a:hover{color:inherit}hr{border:0;clear:both;display:block;width:85%;background-color:#00203fff;height:1px}#hr2{border:0;clear:both;display:block;width:100%;background-color:white;height:1px}a.effect-underline:after{content:'';position:absolute;left:0;display:inline-block;height:1em;width:100%;border-bottom:1px solid;margin-top:10px;opacity:0;-webkit-transition:opacity 0.35s, -webkit-transform 0.35s;transition:opacity 0.35s, -webkit-transform 0.35s;transition:opacity 0.35s, transform 0.35s;transition:opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;-webkit-transform:scale(0, 1);transform:scale(0, 1);color:white}a.effect-underline:hover:after{opacity:1;-webkit-transform:scale(1);transform:scale(1);color:white}#tech-skills:hover{color:inherit}#about-text{font-size:25px;font-weight:bold}#my-name{font-weight:900;text-shadow:2px 2px #108181}

